@import url(https://fonts.googleapis.com/css2?family=Charmonman:wght@700&family=Dekko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #090C9B;
}
.app{
  text-align:center;
  padding-top:5%;
}
.header-img-expanded{
    height:35%;
    width:40%;
    -webkit-animation: expand 1s 1;
            animation: expand 1s 1;
}

.header-img-contracted{
    height:20%;
    width:25%;
    -webkit-animation: contract 1s 1;
            animation: contract 1s 1;
}

@-webkit-keyframes expand {
    from{
        height:20%;
        width:25%;
    }
    to{
        height:35%;
        width:40%;
    }
}

@keyframes expand {
    from{
        height:20%;
        width:25%;
    }
    to{
        height:35%;
        width:40%;
    }
}

@-webkit-keyframes contract {
    from{
        height:35%;
        width:40%;
    }
    to{
        height:20%;
        width:25%;
    }
    
}

@keyframes contract {
    from{
        height:35%;
        width:40%;
    }
    to{
        height:20%;
        width:25%;
    }
    
}

.header-text{
    color:#F2E86D;
    font-family: 'Charmonman', cursive;
}

.header-text-large{
    font-size: 3rem;
    -webkit-animation: increase 1s 1;
            animation: increase 1s 1;
}

.header-text-small{
    font-size:1.5rem;
    -webkit-animation: decrease 1s 1;
            animation: decrease 1s 1;
}

@-webkit-keyframes increase {
    from{
        font-size: 1.5rem;
    }
    to{
        font-size: 3rem;
    }
}

@keyframes increase {
    from{
        font-size: 1.5rem;
    }
    to{
        font-size: 3rem;
    }
}

@-webkit-keyframes decrease {
    from{
        font-size: 3rem;
    }
    to{
        font-size: 1.5rem;
    }
}

@keyframes decrease {
    from{
        font-size: 3rem;
    }
    to{
        font-size: 1.5rem;
    }
}
.search-bar{
    background-color: #090C9B;
    padding: 0.5%;
    width:300px;
    border: 5px solid #F2E86D;
    box-sizing: border-box;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 100px;
    padding-left:54px;
    font-family: 'Dekko', cursive;
    font-size: 1.5rem;
    color:#F2E86D;
    outline:none;
}

i{
    color:#F2E86D;
    z-index: 1;
    position: relative;
    left:40px;
}
.suggestions{
    padding:5% 0;
    justify-content: center;
    align-items: center;
}
.suggestion{
    display: inline-block;
    border: 5px solid #F2E86D;
    box-sizing: border-box;
    border-radius: 100px;
    padding: 0.5%;
    margin: 1%;
    width:250px;
    font-family: 'Dekko', cursive;
    font-size: 1.5rem;
    color:#F2E86D; 
}

.suggestion:hover{
    background-color: #F2E86D;
    color: #090C9B;
}
